@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');

body, html, #root {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: 'Roboto', sans-serif;
  background: linear-gradient(135deg, #0f2027, #203a43, #2c5364);
  color: #fff;
  overflow: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.app-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 90vw;
  max-width: 1200px;
  min-height: 90vh;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.4);
  overflow: hidden;
}

.side-image {
  flex: 1;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  opacity: 0.85;
  transition: opacity 0.3s ease;
}
.side-image:hover {
  opacity: 1;
}
.left-image {
  background-image: url('https://images.unsplash.com/photo-1635865266170-3c90eac30f3f?auto=format&fit=crop&w=600&q=80');
}
.right-image {
  background-image: url('https://images.unsplash.com/photo-1517248135467-4c7edcad34c4?auto=format&fit=crop&w=600&q=80');
}

.content {
  flex: 2;
  padding: 40px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  background: rgba(0, 0, 0, 0.4);
  border-radius: 0 15px 15px 0;
  overflow-y: auto;
  max-height: 100%;
}
.content h1 {
  margin-bottom: 30px;
  font-weight: 700;
  font-size: 2.5rem;
  text-align: center;
  color: #00ffe7;
  text-shadow: 0 0 5px #00ffe7;
}

form {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  margin-bottom: 25px;
  justify-content: center;
}
form input {
  flex: 1 1 150px;
  padding: 12px 15px;
  border-radius: 8px;
  border: none;
  font-size: 1rem;
}
form button {
  flex: 1 1 100px;
  background: #00ffe7;
  border: none;
  border-radius: 8px;
  font-weight: 700;
  color: #000;
  cursor: pointer;
  transition: background 0.3s ease;
}
form button:hover {
  background: #00c2b8;
}

.content h2 {
  margin-bottom: 15px;
  font-weight: 600;
  color: #00ffe7;
  text-shadow: 0 0 3px #00ffe7;
}
ul {
  max-height: 180px;
  overflow-y: auto;
  padding-left: 20px;
  margin-bottom: 30px;
  border-left: 3px solid #00ffe7;
  background: rgba(0, 255, 231, 0.1);
  border-radius: 6px;
}
ul li {
  padding: 8px 0;
  border-bottom: 1px solid rgba(0, 255, 231, 0.3);
}

#advice {
  margin-top: 15px;
}

.content button#get-advice {
  background: #0077ff;
  border: none;
  border-radius: 10px;
  padding: 15px;
  font-size: 1.1rem;
  color: white;
  cursor: pointer;
  width: 100%;
  transition: background 0.3s ease;
  margin-bottom: 15px;
}
.content button#get-advice:hover {
  background: #005bb5;
}
.content button#get-advice.clicked {
  background-color: #004c99;
  transform: scale(0.98);
}

.advice-text {
  font-style: italic;
  font-size: 1.25rem;
  line-height: 1.6;
  color: #a0fff4;
  padding: 20px;
  border-left: 4px solid #00ffe7;
  background: rgba(0, 255, 231, 0.1);
  border-radius: 8px;
  white-space: pre-wrap;
  overflow-y: auto;
  max-height: 250px;
}

.advice-text::-webkit-scrollbar,
ul::-webkit-scrollbar {
  width: 8px;
}
.advice-text::-webkit-scrollbar-track,
ul::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.15);
  border-radius: 4px;
}
.advice-text::-webkit-scrollbar-thumb,
ul::-webkit-scrollbar-thumb {
  background: #00ffe7;
  border-radius: 4px;
}

.new-session-button {
  background: #ff4081;
  border: none;
  border-radius: 10px;
  padding: 12px 20px;
  font-size: 1.05rem;
  color: white;
  font-weight: 600;
  cursor: pointer;
  margin-top: 15px;
  align-self: center;
  width: 100%;
  max-width: 250px;
  transition: background 0.3s ease, transform 0.2s ease;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
}
.new-session-button:hover {
  background: #e73370;
  transform: scale(1.02);
}

.download-report-button {
  background: #00c853;
  border: none;
  border-radius: 10px;
  padding: 12px 20px;
  font-size: 1.05rem;
  color: white;
  font-weight: 600;
  cursor: pointer;
  margin-top: 15px;
  align-self: center;
  width: 100%;
  max-width: 250px;
  transition: background 0.3s ease, transform 0.2s ease;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
}
.download-report-button:hover {
  background: #009c3d;
  transform: scale(1.02);
}

/* Update here */
@media screen {
  .hidden-print {
    display: none;
  }
}

@media print {
  .hidden-print {
    display: block;
  }
}

.toggle-button {
  background: #00c8ff;
  border: none;
  border-radius: 10px;
  padding: 12px 20px;
  font-size: 1rem;
  color: #000;
  font-weight: 600;
  cursor: pointer;
  margin: 10px 0;
  transition: background 0.3s ease, transform 0.2s ease;
  width: 100%;
  max-width: 250px;
  align-self: center;
}
.toggle-button:hover {
  background: #00a3d6;
  transform: scale(1.03);
}

/* Responsive */
@media (max-width: 900px) {
  .side-image.left-image,
  .side-image.right-image {
    display: none;
  }

  .app-container {
    flex-direction: column;
    align-items: center;
    height: auto;
  }

  .content {
    border-radius: 15px;
    width: 90vw;
    padding: 30px 20px;
  }
}
